<template>
    <v-app>
        <v-container fluid>
            <v-row justify="center">
                <v-col cols="12" md="9">
                    <v-list-item>
                        <v-list-item-content>
                            <TravelCard v-for="travel in travels" v-bind:key="travel.id" v-bind:travel="travel"
                            v-on:deleteTravel="deleteTravel"  v-on:editTravel="editTravel" />
                        </v-list-item-content>
                    </v-list-item>
                    <v-row>
                        <v-col cols="10">
                        </v-col>
                        <v-col cols="2">
                            <v-btn outlined @click="isCreateTravel=true" color="secondary">
                                <v-icon>
                                    mdi-pencil
                                </v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>
                    <v-dialog v-model="isCreateTravel" max-width="800px" v-if="isCreateTravel">
                        <v-card>
                            <v-container>
                                <v-row>
                                    <v-col cols="3">
                                        <v-text-field v-model="date" label="" v-on:click="isShowDatePicker=true"></v-text-field>
                                            <v-dialog v-model="isShowDatePicker" max-width="400px">
                                                <v-date-picker v-model="date"/>
                                            </v-dialog>
                                    </v-col>
                                    <v-col cols="6" align-self="center">
                                        <v-text-field v-model="name" label=""></v-text-field>
                                    </v-col>
                                    <v-col cols="2" align-self="center">
                                        <v-btn @click="createTravel"></v-btn>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card>
                    </v-dialog>
                </v-col>
            </v-row>
        </v-container>
    </v-app>
</template>

<script>
  //import HelloWorld from '../components/HelloWorld'
  import sampleData from '../consts/sample-data'
  import TravelCard from '../components/TravelCard.vue'

  export default {
  components: { TravelCard },
    name: 'MyTravels',
    data () {
        return {
            debug: false,
            travels: [],
            isCreateTravel: false,
            isShowDatePicker: false,
            date: new Date().toISOString().substr(0, 10),
            name:''
        }
    },
    props: {
    },
    computed:{
    },
    methods: {
        createTravel: function(){
            console.log('travelCreate')
            const newTravel = {
                userId: this.$store.state.user.id,
                date: new Date(this.date),
                name: this.name
            }
            //console.log(newTravel)
            this.axios.post('https://pure-gorge-50096.herokuapp.com/api/createtravel',newTravel)
            .then((response) => {
                console.log(response)
                this.travels = response.data
            })
            .catch((e) => {
                alert(e);
            })
            this.isCreateTravel = false
        },
        deleteTravel: function(travel){
            console.log('travel')
            this.axios.delete('https://pure-gorge-50096.herokuapp.com/api/deletetravel',{
            params: {
                userId: travel.userId,
                travelId: travel.id
            }})
            .then((response) => {
                this.travels = response.data
            })
            .catch((e) => {
                alert(e)
            })
        },
        editTravel: function(travel){
            console.log('travel')
            console.log(travel)
            this.$store.commit('changeTmpTravel', travel)
            //travel.datedateyyyy/mm/ddString
            this.$router.push({ name: "MyScheduleChart"}).catch(() => {}) 
        },
    },
    mounted() {
        //travelIdtravels  
        console.log('GET')
        this.axios.get('https://pure-gorge-50096.herokuapp.com/api/showtravel',{
            params: {
                userId: this.$store.state.user.id
            }
        })
          .then((response) => {
              console.log(response)
              this.travels = response.data
          })
          .catch((e) => {
            alert(e)
          })
    },
  }
</script>

