<template>
    <v-app>
        <v-container fluid>
            <v-row justify="center">
                <v-col cols="12" md="9">
                        <v-row class="ma-1">
                            {{`${$store.state.travel.date}${$store.state.travel.name}`}}
                            <v-spacer></v-spacer>
                            <v-btn class="mx-1" fab dark color="secondary" @click="isCreateSchedule=true" elevation="0">
                                <v-icon dark>
                                    mdi-pencil
                                </v-icon>
                            </v-btn>
                        </v-row>
                    <v-list-item>
                        <v-list-item-content>
                            <ScheduleCard v-for="schedule in schedules" v-bind:key="schedule.name" v-bind:schedule="schedule"
                            v-on:deleteSchedule="deleteSchedule" v-on:editSchedule="editSchedule"/>                    
                        </v-list-item-content>
                    </v-list-item>
                </v-col>
            </v-row>
            <v-dialog v-model="isCreateSchedule" max-width="800px" v-if="isCreateSchedule">
                <ScheduleForm v-on:submitNewSchedule="recordSchedule" :mode="'create'"/>
            </v-dialog>
            <v-dialog v-model="isEditSchedule" max-width="800px" v-if="isEditSchedule">
                <ScheduleForm :mode="'edit'" :schedule="tmpSchedule" v-on:updateSchedule="updateSchedule"/>
            </v-dialog>
        </v-container>
    </v-app>
</template>

<script>
  //import HelloWorld from '../components/HelloWorld'
  import sampleData from '../consts/sample-data'
  import spotList from '../consts/spot-list'
  import About from './About.vue'
  import ScheduleCard from '../components/ScheduleCard.vue'
  import ScheduleForm from '../components/ScheduleForm.vue'

  export default {
  components: { ScheduleCard, ScheduleForm },
    name: 'MySchedule',
    data () {
        return {
            debug: false,
            isCreateSchedule:false,
            isEditSchedule:false,
            tmpSchedule:{},
            schedules: []
        }
    },
    props: {
        //travel: Object
    },
    computed:{
    },
    methods: {
        recordSchedule: function(newSchedule){
            console.log('POST')        
            this.axios.post('https://pure-gorge-50096.herokuapp.com/api/createschedule',{
                travelId: this.$store.state.travel.id,
                schedule: newSchedule
            })
            .then((response) => {
                console.log(response)
                this.schedules = response.data.map(obj=>{
                    //spotCodespotlist
                    const spot = spotList.filter(spot => {
                        if(spot["Code"] == obj.spotCode){
                            return spot
                        }
                    })
                    obj.spot = spot[0]
                    return obj
                })
            })
            .catch((e) => {
                alert(e);
            })
            this.isCreateSchedule = false
        },
        updateSchedule: function(newSchedule){
            console.log('UPDATE')
            console.log(newSchedule)
            this.axios.put('https://pure-gorge-50096.herokuapp.com/api/updateschedule',{
                scheduleId: newSchedule.id,
                travelId: newSchedule.travelId,
                startTime: newSchedule.startTime,
                spotCode: newSchedule.spot.Code,
                memo: newSchedule.memo
            })
            .then((response) => {
                console.log(response);
                this.schedules = response.data.map(obj=>{
                    //spotCodespotlist
                    const spot = spotList.filter(spot => {
                        if(spot["Code"] == obj.spotCode){
                            return spot
                        }
                    })
                    obj.spot = spot[0]
                    return obj
                })
            })
            .catch((e) => {
                alert(e);
            })
            this.isEditSchedule = false
        },
        deleteSchedule: function(schedule){
            console.log('schedules')
            console.log(this.schedules)
            this.axios.delete('https://pure-gorge-50096.herokuapp.com/api/deleteschedule',{
            params: {
                scheduleId: schedule.id,
                travelId: schedule.travelId
            }})
            .then((response) => {
                console.log(response)
                this.schedules = response.data.map(obj=>{
                    //spotCodespotlist
                    const spot = spotList.filter(spot => {
                        if(spot["Code"] == obj.spotCode){
                            return spot
                        }
                    })
                    obj.spot = spot[0]
                    return obj
                })
            })
            .catch((e) => {
                alert(e)
            })
            console.log('schedules')
            console.log(this.schedules)
        },
        editSchedule: function(schedule){
            //updateSchedule
            console.log('schedule')
            console.log(schedule)
            this.isEditSchedule = true
            this.tmpSchedule = schedule
        }

    },
    mounted() {
        //travelIdschedules  
        console.log('GET')
        this.axios.get('https://pure-gorge-50096.herokuapp.com/api/showschedule',{
            params: {
                travelId: this.$store.state.travel.id
            }
        })
          .then((response) => {
            console.log(response)
            this.schedules = response.data.map(obj=>{
                //spotCodespotlist
                const spot = spotList.filter(spot => {
                    if(spot["Code"] == obj.spotCode){
                        return spot
                    }
                })
                obj.spot = spot[0]
                return obj
            })
          })
          .catch((e) => {
            alert(e)
          })
        //console.log(this.$store.state.travel)
    },


  }
</script>

